import React from "react";
import { checkWin } from "./Message";
const Score = ({ win, loss }) => {
  return (
    <div className="score-container">
      <div className="score">
        <p>
          Gagnés: {win} {win == 1 ? "Niveau Sacha du Bourg-Palette" : ""}
          {win == 2 ? "1er badge d'arène" : ""}
          {win == 3 ? "2e badge d'arène" : ""}
          {win == 4 ? "3e badge d'arène" : ""}
          {win == 5 ? "4e badge d'arène" : ""}
          {win > 5 && win < 9 ? "X badge d'arène (zzz)" : ""}
          {win > 9 ? "Maître de la ligue!!!" : ""}
          {win > 10 ? "Professeur Chen" : ""}
        </p>

        <p>Perdus: {loss}</p>
      </div>
    </div>
  );
};

export default Score;
